@include exports("icons") {

    .icon-definitions {
        display: none;
    }

    .material-icons, .img-icon {
        position:relative;
        width: 1em;
        height: 1em;

        /* Support for all WebKit browsers. */
        -webkit-font-smoothing: antialiased;
        /* Support for Safari and Chrome. */
        text-rendering: optimizeLegibility;

        /* Support for Firefox. */
        -moz-osx-font-smoothing: grayscale;

        /* Support for IE. */
        font-feature-settings: 'liga';

        &:before {
            position: absolute;
            top:0;
            left:0;
        }

        svg {
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            width: 100%;
            height: 100%;
            fill: white;
        }
    }

    // Can use as pseudo elemtents shown below or or add the commented name to the <i> element
    // e.g <i class="material-icons">open_in_new</i>
