@import "../../../components/miyagi-core/styles/variables";
@import "../../../components/miyagi-core/styles/mixins";

.testimonial {
    padding-bottom: 1.4rem;

    @include bp(tab-up) {
        height: 60.9rem;
        background: rgba(0,0,0,0);
    }
}

.testimonial__backdrop {

    @include bp(tab-up) {
        width: 100%;
        overflow: hidden;
        text-align: center;
    }
}

.testimonial__backdrop__img {
    max-width: 100%;
    display: block;
    width: 100%;

    @include bp(tab-up) {
        width: auto;
        max-width: none;
        position: relative;
        left: 50%;
        transform: translate(-50%, 0);
    }
}

.testimonial__poster {
    width: 100%;
    display: block;
    margin: auto;
}

.testimonial__card-ctn {
    max-width: $ctn-max-width;
    width: 100%;
    padding: 0 0.8rem;
    margin-top: -0.7rem;

    @include bp(tab-up) {
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        margin: auto;
        padding: 9.7rem map-get($keyline, small) map-get($keyline, small) 7%;
        background: rgba(white, 0);
    }
}

.testimonial-card {
    @extend %primary-font-light;
    position: relative;
    max-width: 38.7rem;
    padding: 3.4rem map-get($keyline, large) 2.1rem;
    margin: -6.2rem auto 0;
    text-align: left;

    background-color: map-get($color, subpage-bg);

    @include bp(tab-up) {
        padding: 4.7rem map-get($keyline, large) 2.2rem;
        margin: 0;
    }
}



.testimonial-card__heading {
    font-size: 2.4rem;
    line-height: 3.2rem;
    color: map-get($color, action-dark-87);
    margin-bottom: 2.2rem;

    @include bp(tab-up) {
        font-size: 2.8rem;
        line-height: 4rem;
        margin-bottom: 2.2rem;
    }
}

p.testimonial-card__copy {
    margin-bottom: 1.1rem;
    color: map-get($color, action-dark-54);

    @include bp(tab-up) {
        margin-bottom: 2.0rem;
    }
}

.testimonial__video-cta {
    display: inline-table;

    .testimonial__video-cta-icon {
        display: inline-block;
        top: -0.1rem;
        margin-right: 0.4rem;
        vertical-align: middle;
    }
}


// Homepage specific testomonials styling.
.testimonials {

    background-color: palette(white, base);
    text-align: center;
    overflow: hidden;

    md-content {
        overflow: hidden;
        background-color: rgba(0, 0, 0, 0);
    }
}


.testimonials__title-text {
    @extend %secondary-font-light;
    padding-top: 3.5rem;
    font-size: 1.4rem;
    color: map-get($color, action-dark-54);
    line-height: 2rem;

    @include bp(tab-up) {
        padding-top: 5.9rem;
        font-size: 2rem;
        letter-spacing: 0.2px;
        line-height: 2.6rem;
        margin-bottom: 1.3rem;
    }
}

.testimonials__carousel {
    position: relative;
}

// // Testimonials Tabs Overrides
.testimonials__tabs {

    // without this the links on mobile aren't shown once the arrows are.
    md-tabs-canvas.md-paginated {
        margin: 0 3.2rem;
        width: 100%;
    }

    md-tabs-wrapper md-prev-button md-icon {
        margin-top: 0.7rem;
    }

    md-tabs-wrapper md-next-button md-icon {
       margin-top: 0.4rem;
    }

    // Without this, there's a bug on IE where the `md-pagination-wrapper`
    // element is 141005.4 pixels wide, meaning that the tab titles don't
    // display
    .md-center-tabs {
        max-width:100%;
    }

    .md-tab {
        &:first-of-type {
            margin-left: 0;
        }

        &.md-active {
            color: palette(sanMarino, base);
        }
    }

    &.md-default-theme md-tabs-wrapper {
        height: 6.6rem;
        display: flex;
        align-items: flex-end;
        justify-content: center;

        @include bp(tab-up) {
            padding: 0;
        }
    }

    &.md-default-theme md-ink-bar {
        color: palette(sanMarino, base);
        background-color: palette(sanMarino, base);
        height: 0.4rem;
    }
}