.section__inner-wrap {
    max-width: 81rem;
    margin: auto;
}

.section__label {
    @extend %secondary-font-light;
    color: map-get($color, action-dark-54);
    font-size: 1.6rem;
    line-height: (20 / 16);
    letter-spacing: 0.5px;
    margin-top: 3.1rem;
    text-align: center;

    @include bp(tab-up) {
        font-size: 2.2rem;
        line-height: (29 / 22);
        margin-top: 7rem;
    }
}

.section__heading {
    font-size: 3.6rem;
    margin-top: 8.5rem;
    text-align: left;
}


// --------------------------
// Mission Statement
// --------------------------

.mission {
    background: palette(sanMarino, base);
    min-height: 21.7rem;
    padding: 2.4rem 1.9rem 2.8rem 1.9rem;
    text-align: center;

    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;

    @include bp(tab-up) {
        padding: 5.0rem 1.9rem 5.0rem 1.9rem;
        min-height: 22.4rem;
    }
}

.mission__inner {
    max-width: 82.2rem;
}

.mission__title {
    @extend %secondary-font-light;
    color: palette(white, base);
    font-size: 2rem;
    line-height: (32 / 20);
    margin-bottom: 1.5rem;

    @include bp(tab-up) {
        font-size: 2.8rem;
        line-height: (32 / 28);
        margin-bottom: 3.5rem;
    }
}

.mission__statement {
    @extend %primary-font-light;
    color: palette(white, base);
    font-size: 1.6rem;
    line-height: (24 / 16);

    @include bp(tab-up) {
        font-size: 1.8rem;
        line-height: (28 / 18);
    }
}

.md-button.md-fab.mission__fab {
    position: absolute;
    top: -2.8rem;
    right: 2.6rem;
    z-index: map-get($z-index, hero-fab);

    // background-color and box-shadow come from Angular Material, which we
    // sadly need to override here
    transition-property: opacity, background-color, box-shadow;
    opacity: 1;

    // Hide scroll button once scrolled
    .js-page-wrapper--page-scrolled & {
        opacity:0;
    }
}


// --------------------------
// How It Works
// --------------------------

.how-it-works {
    overflow: hidden;
    padding: 0 map-get($keyline, small);
    background: white;

    @include bp(tab-up) {
        padding: 0 0 17.8rem 0;
    }

    .slick-slider {
        margin-bottom: 0;
    }

    .slick-list {
        position: relative;
    }

    .slick-dots {
        $dotHeight: 0.4rem;
        $dotWidth: 2rem;

        height: $dotHeight;
        margin: 4rem 0;
        position: static;

        @include bp(tab-up) {
            position: absolute;
            margin: 12.5rem 0 4.8rem;
            bottom: -17.8rem;
        }

        li {
            width: 1.5rem;
            height: 0.3rem;
            padding: 0.2rem;
            background-color: map-get($color, action-dark-15);
            vertical-align: top;
            transition: background-color .2s;
            margin-right: 1px;

            @include bp(tab-up) {
                width: $dotWidth;
                height: $dotHeight;
            }

            &.slick-active {
                background-color: palette(sanMarino, base);
            }

            > button {
                display: none;
            }
        }
    }

    .slick-prev,
    .slick-next {
        display: none !important;

        @include bp(tab-up) {
            transform: scaleX(1);
            display: block !important;
            position: absolute;
            bottom: -15.5rem;
        }

        @include bp(dt-up) {
            top: 50%;
            transform: scaleX(1) translateY(-50%);
            margin: 0;
            bottom: auto;
        }

        &:before {
            width: 20px;
            height: 40px;
        }
    }

    .slick-prev {
        left: 2%;

        @include bp(tab-up) {
            left: map-get($keyline, large);
        }

        @include bp(max-out) {
            left: 10%;
        }
    }

    .slick-next {
        right: 2%;

        @include bp(tab-up) {
            right: map-get($keyline, large);
        }

        @include bp(max-out) {
            right: 10%;
        }

        &:before {
            position: absolute;
            top: 50%;
            right: 0;
            transform: rotate(180deg);
            margin-top: -2rem;
        }
    }
}

$hiw-car-width: 100%;
$hiw-max-width: 840;
$hiw-box-width: 840; // $hiw-box-width - $gutter*2
$hiw-img-ctn: 407;
$hiw-img-max-height: 288;
$hiw-img-gutter: 55;
$hiw-img-ctn: $hiw-img-ctn + $hiw-img-gutter;
$hiw-content-ctn: $hiw-max-width - $hiw-img-ctn;
$hiw-inner-width: 433;
$hiw-slide-gutter: 0;

.hiw-carousel {
    max-width: $hiw-car-width;
    margin: 1.4rem auto 0;
    width: 100%;

    @include bp(tab-up) {
        margin-top: 9.1rem;
    }
}

.hiw-slide {
    display: none;
    padding: 0 map-get($keyline, large);

    @include bp(dt-up) {
        padding: 0 map-get($keyline, small);
    }
}

.hiw-slide__contents {
    max-width: $hiw-max-width+px;
    margin: auto;
    padding: $hiw-slide-gutter;
    position: relative;
    text-align: center;

    @include bp(tab-up) {
        overflow: hidden;
        display: flex;
        flex-direction: row;
        text-align: left;
        align-content: stretch;
        align-items: stretch;
    }
}

.hiw-slide__placeholder {
    @include bp(tab-up) {
        width: percentage($hiw-img-ctn / $hiw-box-width);
        display: block;
    }

    &:after {
        @include bp(tab-up) {
            content: "";
            display: block;
            padding-bottom: percentage($hiw-img-max-height / $hiw-img-ctn);
        }
    }
}

.hiw-slide__inner {
    @include bp(tab-up) {
        width: percentage($hiw-content-ctn / $hiw-box-width);
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-content: stretch;
    }
}

.hiw-slide__img-ctn {
    @include bp(tab-up) {
        position: absolute;
        top: $hiw-slide-gutter;
        left: $hiw-slide-gutter;
        right: percentage($hiw-inner-width / $hiw-box-width);
        bottom: $hiw-slide-gutter;
    }
}

.hiw-slide__img {
    max-width: 21.2rem;
    display: block;
    margin: auto;

    @include bp(tab-up) {
        max-width: 100%;
    }
}

.hiw-slide__heading {
    @extend %secondary-font-light;
    color: palette(sanMarino, base);
    font-size: 2rem;
    line-height: (30 / 20);
    margin-top: 0;
    margin-bottom: 3.7rem;
    letter-spacing: 0.55px;

    @include bp(tab-up) {
        font-size: 2.8rem;
        line-height: (28 / 28);
        margin-bottom: 2.7rem;
    }
}

.hiw-slide__text {
    color: map-get($color, action-dark-54);
    font-size: 1.6rem;
    line-height: (24 / 16);
    letter-spacing: 0.05px;
    margin: 3.3rem auto 0;
    max-width: 28.2rem;

    @include bp(tab-up) {
        font-size: 1.8rem;
        line-height: (28 / 18);
        margin-top: 0;
        max-width: 100%;
    }
}


// --------------------------
// Freeform Module
// --------------------------

.freeform {
    overflow: hidden;
    background-color: white;

    @include bp(tab-up) {
        padding: 0 3.2rem 9.2rem;
    }
}

.freeform__ctn {

    position: relative;
    max-width: 28.2rem;
    margin: 0 auto;
    overflow: hidden;

    @include bp(tab-up) {
        max-width: 100rem;
        overflow: hidden;
        display: flex;
        flex-direction: row;
        text-align: left;
        align-content: stretch;
        align-items: stretch;
        margin-top: 5.8rem;
    }
}

.freeform__content {

    @include bp(tab-up) {
        width: 50%;
        max-width: 43.4rem;
        padding-left: (12 + 8)px;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-content: stretch;
    }
}

.freeform__heading {
    @extend %secondary-font-light;

    margin: 2.6rem 0 1.4rem;
    font-size: 2.2rem;
    line-height: 1.4em;
    text-align: center;
    color: palette(sanMarino, base);


    @include bp(tab-up) {
        margin: 6.6rem 0 3.4rem;
        font-size: 2.8rem;
        text-align: left;
    }
}

.freeform__placeholder {

    @include bp(tab-up) {
        width: 50%;
        display: block;
    }

    &:after {
        @include bp(tab-up) {
            content: "";
            display: block;
            padding-bottom: 62%;
        }
    }
}

.freeform__img-ctn {
    margin: 0 auto 1.6rem;

    @include bp(tab-up) {
        position: absolute;
        top: 0;
        left: 0;
        right: 58%;
        bottom: 0;
    }
}

.freeform__img {
    width: 100%;
    display: block;
}

p.freeform__text { /*p for specifity*/
    @extend %primary-font-light;
    color: map-get($color, action-dark-54);
    font-size: 1.6rem;
    line-height: (24 / 16);
    text-align: center;

    @include bp(tab-up) {
        font-size: 1.8rem;
        line-height: (28 / 18);
        margin-bottom: 1rem;
        text-align: left;
    }
}

.freeform__link {
    margin: 3.0rem 0;
    text-align: center;
    text-transform: uppercase;

    @include bp(tab-up) {
        text-align: left;
    }
}