.signup-dialog__dialog {
	max-width: 52rem;
    max-height: none;
    margin: 1rem;
}

.signup-dialog__dialog-content {

    height: auto;
    padding: 0;
    overflow: hidden;
}

.signup-dialog__header {
    position: relative;
    width: 100%;
    overflow: hidden;

}

.signup-dialog__graphic {

	img {
	    display: block;
	    width: 100%;
	}
}

.signup-dialog__content {
	padding: 2rem 2rem 1rem;

	@include bp(tab-up) {
		padding: 3rem;
	}
}

.signup-dialog__title {
	@extend %secondary-font-light;

	margin: 0 0 1.4rem;
	font-size: 1.8rem;
	line-height: (24 / 18);

	@include bp(tab-up) {
		margin: 0 0 2rem;
		font-size: 2.8rem;
		line-height: (32 / 28);
		letter-spacing: 0.65;
	}
}

.signup-dialog__copy {

	&, p, ul, li {
		@extend %primary-font-light;

		font-size: 1.6rem;
		line-height: (24 / 16);
		color: palette(doveGray, base);
	}

	p {
		margin-bottom: 1.2rem;
	}

	ul {
		margin: 0;
		padding: 0 0 0 2rem;
	}

	li {
		margin-bottom: 1.2rem;

		@include bp(tab-up) {
			margin-bottom: 0;
		}
	}
}

.signup-dialog__actions {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: flex-end;
    align-content: center;
    align-items: center;

    padding: 0 2rem 2rem;
}

.signup-dialog__close {
    order: 0;
    flex: 0 1 auto;
    align-self: auto;

    color: palette(doveGray, base);
}

.signup-dialog__signup {
    order: 0;
    flex: 0 1 auto;
    align-self: auto;
}