@import "../../../components/miyagi-core/styles/variables";
@import "../../../components/miyagi-core/styles/mixins";

.tout {
}

.tout__link {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;

    background-color: palette(caribbeanGreen, base);
    color: palette(white, base);

    min-height: 11.2rem;
    padding: 3rem 2rem;

    cursor: pointer;
    transition: background-color 300ms;

    &:hover,
    &:focus {
        background-color: palette(caribbeanGreen, light);
    }

    @include bp(tab-up) {
        padding: 3rem 9rem;
    }
}

.tout__subtitle {
    @extend %primary-font-med;
    font-size: 1.5rem;
    line-height: (18 / 15);
    padding-right: 3rem;
    margin-bottom: 0.8rem;
}

.tout__title {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
}

.tout__title-text {
    @extend %primary-font-med;
    font-size: 2rem;
    line-height: (24 / 20);
    margin-right: 0.6rem;
    width: 100%;
}