@import "../../../components/miyagi-core/styles/variables";
@import "../../../components/miyagi-core/styles/mixins";

$copy-max-width: 46rem;

.hero-banner {
    position: relative;

    @include bp(tab-up) {
        height: 68vh;
        min-height: 54rem; // Covers browsers that don't understand viewport units.
        max-height: 68rem;
        overflow: hidden;
    }

    // Generic image settings between the two pseudo elements.
    &::before,
    &::after {
        background-position: left center;
        background-repeat: no-repeat;
        background-size: cover;
    }

    // Tablet-upward image.
    &::before {
        content: none;

        @include bp(tab-up) {
            content: '';
            position: absolute;
            display: block;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
        }
    }

    // Mobile image.
    &::after {
        content: '';
        display: block;
        padding-bottom: percentage(208 / 360);

        @include bp(tab-up) {
            content: none;
        }
    }
}

.hero-banner--light {

    .hero-banner__intro-title,
    p.hero-banner__subtext,
    a.md-button.md-accent, .md-button.md-accent {

        @include bp(tab-up) {
            color: palette(white, base);
        }
    }
}

.hero-banner--title-large {

    .hero-banner__intro-title {

        @include bp(tab-up) {
            font-size: 6.4rem;
            line-height: (80 / 64);
        }
    }
}

.hero-banner--description-large {

    p.hero-banner__subtext {

        @include bp(tab-up) {
            font-size: 2rem;
            line-height: (28 / 20);
            letter-spacing: -0.015em;
        }
    }
}

.hero-banner__content {
    background: map-get($color, subpage-bg);
    max-width: 144rem;
    width: 100%;
    padding: 3.1rem 1.7rem 3.7rem 1.7rem;

    @include bp(tab-up) {
        position: absolute;
        top: 48.5%;
        right: 0;
        left: 0;
        transform: translateY(-50%);
        margin: auto;

        background: none;
        padding: 0 11.2rem;
    }
}

.hero-banner__intro-title {
    @extend %secondary-font-light;
    max-width: $copy-max-width;
    margin-bottom: 2rem;
    font-size: 4rem;
    line-height: (52 / 44);
    color: palette(emperor, base);

    @include bp(tab-up) {
        font-size: 4.4rem;
    }
}

p.hero-banner__subtext {
    @extend %primary-font-light;
    font-size: 1.6rem;
    line-height: (20 / 16);
    letter-spacing: -0.027rem;
    color: palette(black, base);
    max-width: $copy-max-width;
    margin: 0 0 3.8rem;

    @include bp(dt-up) {
        @extend %primary-font-normal;
        font-size: 1.8rem;
        line-height: (28 / 18);
        letter-spacing: normal;
        color: palette(black, base);
        margin: 0 0 2.6rem;
    }
}

.hero-banner__actions {
    margin-top: 3.3rem;

    @include bp(tab-up) {
        margin-top: 0;
    }

    .hero-banner__action {
        min-width: 0;
        padding: 0 1.6rem;
        height: 5.2rem;
        font-size: 1.5rem;
        line-height: 5.2rem;
        letter-spacing: 0.05rem;
    }
}